.mobileartistprofile{
  &__img{
    img{
      display: block;
      width: 100%;
      height: auto;
    }
  }
  &__card{
    color: #F4F3F3;
    font-family: 'Akrobat';
    margin: 16px 16px 24px;
    // padding-bottom: 23px;
    // border-bottom: 0.5px solid #F4F3F3;
  }
  &__date-wrapper{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  }
  &__name{
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 43px;
    text-transform: uppercase;
    margin-bottom: 18px;
  }
  &__description{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 145%;
    margin-bottom: 23px;
  }
}