.jazz-marquee-w {
    position: relative;
    display: block;
    width: calc(100vw - 2.77777vw - 5.555555vw - 320px);
    height: 45vh;
    overflow: hidden;
}

.jazz-marquee {
    position: absolute;
    display: block;
    margin: auto auto;
    white-space: nowrap;
    overflow: hidden;
    min-width: 100%;
    height: 100%;
}

.jazz-marquee span {
    display: inline-block;
    padding-left: 100%;
    font-family: 'Soviet';
    text-align: center;
    color: #F4F3F3;
    white-space: nowrap;
    min-width: 100%;
    height: 100%;
    font-size: 380px;
    animation: marquee 20s linear infinite;
}

.jazz-marquee2 span {
    animation-delay: 10s;
}

@keyframes marquee {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(-100%, 0);
    }
}